.denomination {
	margin: 0 auto 20px;
	border: 2px solid #eee;
	max-width: 600px;
	background: $white;
	&__container {
		display: table;
		table-layout: fixed;
		width: 100%; }
	&__row {
		display: table-row;
		@include clr; }
	&__col {
		display: table-cell;
		padding: 15px 20px 15px;
		width: 50%;
		&:nth-child(1) {
			border-right: 1px solid #eee; }
		&:nth-child(2) {
			border-left: 1px solid #eee; } }
	&__title {
		margin-bottom: 10px;
		@include rmed;
		font-size: 20px;
		line-height: 1; }
	&__section {
		position: relative;
		padding: 5px 40px 5px 0;
		margin: 0 -5px;
		font-size: 0; }
	&__label,
	&__splitter,
	&__field,
	&__result {
		display: inline-block;
		vertical-align: middle;
		padding: 0 5px;
		color: $darkGray;
		font-size: 15px; }
	&__label {
		width: 20%; }
	&__splitter {
		width: 10%; }
	&__result {
		width: 25%;
		text-align: right; }
	&__field {
		position: relative;
		width: 35%; }
	&__input {
		padding: 10px 0;
		border-bottom: 1px solid rgba($darkGray, 0.4);
		width: 100%;
		height: 40px;
		color: $darkGray;
		@include rlight;
		font-size: 15px;
		line-height: normal;
		text-align: right; }
	&__total {
		padding: 15px 20px;
		border-top: 2px solid #eee;
		background: #f8f8f8;
		font-size: 0;
		&-in {
			margin: 0 -5px; } }
	&__button,
	&__total-label,
	&__total-sum {
		display: inline-block;
		vertical-align: middle;
		padding: 5px;
		@include rmed;
		font-size: 18px; }
	&__button {
		width: 20%; }
	&__total-label {
		width: 50%;
		text-align: right; }
	&__total-sum {
		width: 30%;
		text-align: right; }
	&__clear {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		background: none;
		@include vertical;
		.icon {
			display: inline-block;
			vertical-align: middle;
			opacity: 0.5;
			font-size: 32px; } }
	&__display {
		border-bottom: 2px solid #eee; } }

.denomination-notes {
	margin: 0 auto;
	max-width: 600px; }

.sm-description {
	padding-bottom: 20px;
	@include rreg;
	font-size: 18px;
	line-height: 1.3;
	text-align: center; }
