}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea,
button {
  -webkit-appearance: none;
}

input[type="submit"],
button,
a {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner,
a::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

