.switcher {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 50px;
  height: 30px;
  cursor: pointer;
  user-select: none;
}

.switcher:active .switcher__icon:before {
  box-shadow: inset 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.no-touch .switcher:active .switcher__icon:before {
  box-shadow: inset 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.switcher input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}

.switcher input:checked + .switcher__icon:before {
  background: #2e99db;
}

.switcher input:checked + .switcher__icon:after {
  box-shadow: -1px 2px 2px rgba(0, 0, 0, 0.2);
  transform: translate3d(10px, 0, 0);
}

.switcher__icon {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.switcher__icon:before, .switcher__icon:after {
  content: '';
  position: absolute;
}

.switcher__icon:before {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 15px;
  background: #eee;
  transition: background 0.2s ease-in-out, box-shadow 0.1s ease-in-out;
}

.switcher__icon:after {
  top: 50%;
  left: 50%;
  margin: -12px 0 0 -12px;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  background: #fff;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  transform: translate3d(-10px, 0, 0);
  transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}

.switcher_green input:checked + .switcher__icon:before, .switcher_success input:checked + .switcher__icon:before {
  background: #22B745;
}

