.product-activation {
	padding: 20px;
	&__title {
		margin: 0 0 15px; }
	&__items {
		margin: -10px;
		font-size: 0; }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 10px;
		width: 33.33%; }
	&__form {
		display: none;
		max-width: 600px; }
	&__field {
		margin-top: 20px; }
	&__label {
		display: block;
		margin-bottom: 10px;
		@include rmed;
		font-size: 16px;
		line-height: 1.3; }
	&__search {
		position: relative;
		input {
			padding-left: 50px; } }
	&__icon {
		position: absolute;
		top: 50%;
		left: 15px;
		font-size: 20px;
		margin-top: -0.5em; } }
